export default {
    props: {
        data: {
            type: Array,
            required: true
        },
        pageName: {
            type: String,
            required: true
        }
    },
    methods: {
        scrollToPageTop() {
            window.scrollTo({ 
                top: 0, 
                behavior: "smooth" 
            });
        },
        goToNewsContent(id) {
            this.$router.push(`/maxBeauty/${this.pageName}/${id}`);
            this.scrollToPageTop()
        },
        openNewTab(id) {
            const routerData = this.$router.resolve({path: `/maxBeauty/${this.pageName}/${id}`});
            window.open(routerData.href, '_blank');
        },
    }
}
